.project-container {
  height: 500px;

  background-repeat: no-repeat;
  background-size: 100% auto;
  border-radius: 10px;
  box-shadow: 1px 2px 2px #eee, -3px 1px 10px #eee;
}
.overlay {
  height: 100%;
  width: 100%;
  transition: all 3s ease-in-out;
}
.overlay:hover .button-container {
  background-color: rgba(51, 51, 51, 0.6);
  display: flex;
  border-radius: 10px;
}
.project-message header h1::after {
  height: 70px;
  top: -9px;
  position: absolute;
  content: '';
  border-radius: 17px;
  left: 17px;
  width: 10px;
  background: var(--main-color);
}
.button-container a {
  background-color: var(--main-color);
  cursor: pointer;
  width: 140px;
  padding: 7px 0;
  border-right: 1px solid var(--main-color);
  color: #fff;
  border-color: var(--main-color);
  transition: 0.3s;
}
.button-container {
  height: 100%;
  display: none;
  align-items: center;
  justify-content: center;
  transition: all 3s ease-in-out;
}
.button-container a:hover {
  transform: translateY(-5px);
  background-color: var(--main-color);
}
.btn-primary:not(:disabled):not(.disabled):active {
  outline: none;
  background-color: var(--main-color);
}
