.about {
  margin-top: 150px;
  
}
.article-message-about i {
  color: var(--main-color);
  padding-right: 7px;
  display: inline;
}
.article-message-about p {
  color: #7a7777 ;
  font-weight: 900;
  line-height: 35px;
  font-size: 18px;
}
.article-message-about span {
  color: #7a7777 ;
}
td {
  color: #7a7777 ;
  font-weight: 900;
}
td a {
  color: #7a7777 ;
  font-weight: 900;
}
.about-message header .align::after {
  height: 90px;
  top: 6px;
  position: absolute;
  content: "";
  border-radius: 17px;
  left: 17px;
  width: 10px;
  background: var(--main-color);
}
@media only screen and (max-width: 768px) {
  
  .about {
    margin-top: 30px;
  }
}
@media only screen and (max-width: 450px) {
  table{
    font-size: 0.8rem;
  }
  
}
@media only screen and (max-width: 320px) {
  table{
    font-size: 0.58rem ;
  }
  
}