.header-section {
  margin-top: 200px ;
}
.header-message h2 {
  color: var(--main-color);
  font-weight: 700;
  overflow: hidden;
  white-space: nowrap;
  animation: typewriter;
  position: relative;
  animation-duration: 10s;
  animation-timing-function: steps(90, end);
  animation-iteration-count: infinite;
}
.header-message h2::after {
  content: "|";
  position: absolute;
  right: 0;
  animation: caret infinite;
  animation-duration: 1s;
  animation-timing-function: steps(1, end);
}
.article-message p {
  font-size: 18px;
  color: #7a7777;
  line-height: 35px;
  font-weight: 700;
}

@keyframes typewriter {
  0%,
  100% {
    width: 0;
  }
  20%,
  80% {
    width: 10.2em;
  }
}
@keyframes caret {
  0%,
  100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
.article-message a {
  background-color: var(--main-color) ;
  width: 140px;
  padding: 7px 0;
  transition: all 0.3s;
}
.article-message a:hover {
  transform: translateY(-10px);
  background-color: var(--main-color);
}
@media only screen and (max-width: 400px) {
  .header-message h2{
    font-size: 1.5rem;
  }
  .header-section {
    margin-top: 30px ;
  }
}
@media only screen and (max-width: 550px) {
  
  .header-section {
    margin-top: 30px ;
  }
}